.bloom-design-bar{position:fixed;top:0px;width:100%;max-height:48px;padding:10px 20px;background-color:#fafafa;box-shadow:0 0.1px 0.3px rgba(0,0,0,0.06),0 1px 2px rgba(0,0,0,0.2);z-index:99999}.bloom-design-bar a{margin-right:40px}.bloom-design-bar *{margin:0}.design-bar .bloom-design-bar{position:absolute;top:46px}body.design-bar{margin-top:48px}@media screen and (min-width: 783px){.design-bar .bloom-design-bar{position:fixed;top:32px}}
